
:root {
  --data-range-color-code: red;
}

body {
  font-family: "Roboto-Regular";
  overflow: hidden;
}

#app {
  position: relative;
}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
}

h1 {
  color: #636363;
  text-align: center;
  text-transform: uppercase;
  font-weight: 400;
  font-family: 'eucerinawgl-regular-webfont';

}

.v-section {
  height: 800px;
}

a.btn.router-link-active {
  position: fixed;
  left: 15px;
  top: 13px;
}

a.btn.reload-icon {
  position: fixed;
  bottom: 14px;
  left: 14px;
}

.questions-container .btn.back-btn img {
  position: relative;
}

.back-btn-wrapper {
  position: absolute;
  bottom: 20px;
  display: inline-flex;
  right: 0;
}

.btn.back-btn {
  top: 140px;
}

.v-row,
.v-flex {
  display: flex;
}

.btn-language-selector{
font-family: 'Roboto-Regular';
}

.centered-text {
  text-align: center;
}

.v-right-flexed {
  justify-content: flex-end;
}

.btn-light:hover,
.btn-light:not(:disabled):not(.disabled).active,
.btn-light:not(:disabled):not(.disabled):active,
.show > .btn-light.dropdown-toggle {
  background: #f8f9fa;
  border-color: none;
}

@media screen and (min-width: 640px) {
  h1 {
    font-size: 30px;
    line-height: 34px;
  }

  h2 {
    font-size: 28px;
    line-height: 30px;
  }
}

@media screen and (min-width: 960px) {
  h1 {
    font-size: 50px;
    line-height: 54px;
  }

  h2 {
    font-size: 38px;
    line-height: 45px;
  }

  h3 {
    font-size: 24px;
    line-height: 23px;
  }
}

@media screen and (max-width: 959px) {
  h1 {
    font-size: 42px;
    line-height: 45px;
  }
}

@media screen and (max-width: 640px) {
  h1 {
    font-size: 36px;
    line-height: 39px;
  }

  h2 {
    font-size: 28px;
    line-height: 30px;
  }

  h3 {
    font-size: 24px;
    line-height: 26px;
  }

  .v-col + .v-col {
    margin-top: 25px;
  }
}
