.v-grid {
    display: flex;
}

.col-md-4 {
    padding: 0 10px;
}

@media screen and (min-width: 1500px) {
    .container {
        max-width: 1440px;
    }
}

@media screen and (min-width: 960px) and (max-width: 1499px) {
    .container {
        max-width: 1064px;
    }
}

@media screen and (max-width: 959px) {
    .container {
        max-width: 100%;
    }
}

@media screen and (max-width: 639px) {
    .v-grid {
        flex-direction: column;
    }

    .col-1,
    .col-2,
    .col-3,
    .col-4,
    .col-5,
    .col-6,
    .col-7,
    .col-8,
    .col-9,
    .col-10,
    .col-11,
    .col-12 {
        flex: 0 0 100%;
        max-width: 100%;
    }
}